
import { Options, Vue } from "vue-class-component";

@Options({
  data() {
    return {
      drawing: false,
    };
  },
  methods: {
    mousedown() {
      console.log("1");
      this.drawing = true;
    },
    mouseup() {
      console.log("2");
      this.drawing = false;
    },
    mousemove(e: any) {
      if (this.drawing) {
        console.log("3");
        this.draw(e);
      }
    },
    draw(e: any) {
      const canvas = this.$refs.canvas;
      const ctx = canvas.getContext("2d");

      const x = e.offsetX;
      const y = e.offsetY;

      ctx.lineWidth = 10;
      ctx.lineCap = "round";
      ctx.strokeStyle = "black";

      if (!this.drawing) return;
      ctx.beginPath();
      ctx.moveTo(x, y);
      ctx.lineTo(x, y);
      ctx.stroke();
    },
  },
})
export default class HelloWorld extends Vue {}
